import { startBattle } from '../../firebase'
import { generateItems } from '../../gameData/helpers'
import { buildMonsters } from '../../gameData/monsters/builders'

export async function createBattle({monsters=[], itemsSpec=['2Potion'], gil, consequence}){
    try{
    
        const { monsters: newMonsters, exp} = buildMonsters({builds: monsters})
        const items = generateItems(itemsSpec)
        const battle = {
            monsters: newMonsters, rewards: {items:[], exp, gil: Number(gil) || 0, consequence}
        }
        console.log('log: battle', { battle })
        await startBattle(battle)
    }catch(e){
        console.error('log: error', e)
    }
   
}
