import { Button, Grid, TextField } from '@material-ui/core'
import { filter, map } from 'lodash'
import { MuiFormGenerator } from 'mui-form-generator'
import React, { useState } from 'react'
import theme from '../../../core/theme'
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import { createBattle } from '../../../common/hooks/useCreateBattle'
import { convertMonstersIntoOptions } from '../../../gameData/helpers'

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    flexWrap: 'wrap',
    listStyle: 'none',
    padding: theme.spacing(0.5),
    margin: 0,
  },
  chip: {
    margin: theme.spacing(0.5),
  },
}));

const bluePrint =({type, options})=> ({
    Rows: [
        {
            Cols: [
                {
                    Input: {
                        id: type,
                        name: type,
                        type: 'selectNative',
                        label: type,
                        variant: 'filled',
                        options,

                    }
                },
            ]
        },
        {
            Cols: [
                {
                    Input: {
                        id: `level${type}`,
                        name:  `level${type}`,
                        type: 'number',
                        label: 'Level',
                        variant: 'filled',

                    }
                },
                {
                    Input: {
                        id: `quantity${type}`,
                        name:  `quantity${type}`,
                        type: 'number',
                        label: 'Quantity',
                        variant: 'filled',

                    }
                },
                
            ]
        },
        
        {
            Cols: [
                {
                    Button: {
                        id: `submit${type}`,
                        name: 'submit',
                        type: 'submit',
                        label: 'Add to battle',
                        fullWidth: true,
                        variant: 'outlined',
                    }
                },
            ]
        },
       

    ]
})
const CreateBattleForm = () => {
    const classes = useStyles();
    const [monsterFilter, setmonsterFilter] = useState('')
    const [gil, setgil] = useState(0)
    const [consequence, setconsequence] = useState('')
    const [desiredMonstersToAdd, setdesiredMonstersToAdd] = useState([])
    const options = convertMonstersIntoOptions()
    const filteredOptions = filter(Object.keys(options), monsterType=>{
        if(monsterType.includes(monsterFilter))return true
        return false
    })

    function handleFilter(e){
        setmonsterFilter(e.target.value)
    }
    async function generateBattle(){
        const battleConfig = {
            monsters: map(desiredMonstersToAdd, mon=>({...mon, level: 5})),
            itemsSpec: [],
            gil,
            consequence,
        }

        await createBattle(battleConfig)
    }

    const handleDelete = (index) => () => {
        setdesiredMonstersToAdd((desiredMonstersToAdd) => desiredMonstersToAdd.filter((_, i) => i !== index));
      };
      
    return (
            <Grid spacing={5} container>
                <Grid item xs={12}>
        <TextField fullWidth variant="filled" label="Search..." onChange={handleFilter}/>
                </Grid>
                <Grid item xs={12}>
                <TextField type="number" variant="filled" label="Gil" onChange={e=>setgil(e.target.value)}/>
                <TextField fullWidth type="textarea" variant="filled" label="Consequence" onChange={e=>setconsequence(e.target.value)}/>
                </Grid>

            {map(filteredOptions, (monsterType, i)=>{
                const monsterOptionsInType = options[monsterType]
                const initialValues = {[`level${monsterType}`]: 5, [`quantity${monsterType}`]: 0, [monsterType]: monsterOptionsInType[0].value}
                
                function handleSubmit(values, formik){
                    const mutatedValues = {...values, label: values[monsterType], quantity: values[`quantity${monsterType}`]}
                    setdesiredMonstersToAdd([...desiredMonstersToAdd, mutatedValues])
                    formik.resetForm()
                }
                return(
                    <Grid key={i} item>
                    <MuiFormGenerator
                    theme={theme}
                    blueprint={bluePrint({type: monsterType, options: monsterOptionsInType})}
                    initialValues={initialValues}
                    handleSubmit={handleSubmit}
                />
                </Grid>
                )
            })
            }
            <Grid item xs={12}>
            <Paper component="ul" className={classes.root}>

                {map(desiredMonstersToAdd, (data, i) => {
                    return <Chip
                    key={i}
                        label={`${data.quantity} ${data.label}`}
                        onDelete={handleDelete(i)}
                        className={classes.chip}
                    />
                })}
            </Paper>
            </Grid>

            <Button onClick={generateBattle} fullWidth>Generate Battle</Button>
            </Grid>
    )
}

export default CreateBattleForm

